export default {
  'GET_PROJECT' ({ project }) {
    return project;
  },

  'GET_WORK' ({ work }) {
    return work;
  },

  'GET_EXPERIENCE' ({ experience }) {
    return experience;
  },

  'GET_SKILL' ({ skill }) {
    return skill;
  },

  'GET_SOCIAL_NETWORKS' ({ socialNetworks }) {
    return socialNetworks;
  },

  'GET_PLATAFORMS' ({ plataforms }) {
    return plataforms;
  },
};



